@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.button {
  display: inline-block;
  box-sizing: border-box;
  font-size: 16px;
  line-height: 20px;
  color: #ffffff;
  text-transform: uppercase;
  background-color: #68b738;
  text-align: center;
  border: none;
  cursor: pointer;
}
.page .page__body {
  margin: 0 auto;
  width: 320px;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  box-sizing: border-box;
  color: #000000;
  background-color: #ffffff;
}
.page-header {
  padding-top: 10px;
  padding-bottom: 17px;
}
.page-header .page-header__container {
  margin: 0 20px;
}
.header-navigation .header-navigation__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.header-navigation .header-navigation__item {
  font-size: 18px;
  line-height: 24px;
  text-transform: uppercase;
}
.header-navigation .header-navigation__link {
  text-decoration: none;
  color: #000000;
}
.promo {
  margin-bottom: 20px;
}
.promo .promo__container {
  background-color: rgba(104, 183, 56, 0.85);
}
.promo .promo__wrapper {
  padding-top: 27px;
  margin: 0 20px;
}
.promo .promo__title {
  margin-top: 0;
  margin-bottom: 25px;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  color: #ffffff;
}
.promo .promo__description {
  display: block;
  line-height: 14px;
  text-transform: uppercase;
  color: #ffffff;
  margin-bottom: 29px;
}
.promo .promo__image {
  display: block;
}
.promo .promo__container-button {
  background-color: #ffffff;
  padding-top: 138px;
  margin: 0 20px;
}
.promo .promo__link {
  min-width: 244px;
  text-decoration: none;
  padding: 11px 44px;
  font-size: 14px;
  line-height: 18px;
}
.programs {
  margin: 0 20px 20px;
}
.programs .programs__item {
  background-color: #f2f2f2;
  padding: 30px 33px 18px 20px;
  margin-bottom: 20px;
}
.programs .programs__item:last-child {
  margin-bottom: 0;
}
.programs .programs__item-title {
  margin-top: 0;
  margin-bottom: 29px;
  font-weight: 400;
  text-transform: uppercase;
  font-size: 24px;
  line-height: 37px;
}
.programs .programs__item-description {
  font-family: "Arial", sans-serif;
  color: #444444;
  margin-top: 0;
  margin-bottom: 38px;
}
.programs .programs__item-link {
  font-size: 16px;
  line-height: 16px;
  text-decoration: none;
  color: #000000;
  text-transform: uppercase;
}
.instruction {
  margin: 0 20px 49px;
}
.instruction .instruction__title {
  margin-top: 0;
  margin-bottom: 40px;
  font-size: 36px;
  line-height: 40px;
  font-weight: 400;
}
.instruction .instruction__list {
  font-family: "Arial", sans-serif;
  color: #444444;
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.instruction .instruction__item {
  margin-left: 20px;
}
.example {
  padding-top: 25px;
  padding-bottom: 40px;
  background-color: #eaeaea;
}
.example .example__container {
  margin: 0 20px;
}
.example .example__title {
  margin-top: 0;
  margin-bottom: 40px;
  font-size: 36px;
  line-height: 40px;
  font-weight: 400;
}
.example .example__description {
  font-family: "Arial", sans-serif;
  color: #444444;
  margin-bottom: 20px;
}
.example .example__results {
  margin: 0;
  padding: 0;
  margin-bottom: 20px;
}
.example .example__data {
  font-size: 24px;
  line-height: 24px;
  margin-bottom: 7px;
}
.example .example__parameters {
  font-family: "Arial", sans-serif;
  font-size: 12px;
  line-height: 12px;
  color: #444444;
}
.example .example__price {
  margin: 0;
  margin-bottom: 40px;
  line-height: 20px;
  text-transform: uppercase;
}
.example .example__photo-changes {
  display: block;
  margin-bottom: 15px;
}
.example .example__photo-text {
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
}
.contacts {
  padding-top: 26px;
  padding-bottom: 26px;
  background-color: #ffffff;
}
.contacts .contacts__container {
  margin: 0 20px;
}
.contacts .contacts__slogan {
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  color: #111111;
}
.contacts .contacts__address {
  font-family: "Arial", sans-serif;
  color: #444444;
  line-height: 20px;
}
.page-footer {
  padding-top: 39px;
  padding-bottom: 22px;
  background-color: #f2f2f2;
}
.page-footer .page-footer__container {
  margin: 0 20px;
}
.page-footer .page-footer__logo {
  padding-bottom: 32px;
}
.page-footer .page-footer__logo-academy {
  padding-top: 28px;
  padding-bottom: 32px;
}
.page-footer .page-footer__logo-academy-link {
  font-family: "Arial", sans-serif;
  text-decoration: none;
  font-size: 16px;
  line-height: 18px;
  color: #444444;
}
.socials {
  padding: 20px;
}
.socials .socials__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.catalog-wrapper {
  margin: 0 20px;
  padding-top: 27px;
}
.main-title {
  margin: 0;
  margin-bottom: 49px;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
}
.catalog-basic {
  padding-bottom: 25px;
}
.catalog-basic .catalog-basic__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.product {
  padding-top: 11px;
  padding-bottom: 14px;
  margin-bottom: 19px;
}
.product:nth-last-of-type(2) {
  margin-bottom: 36px;
}
.product:last-child {
  margin-bottom: 0;
}
.product .product__link {
  text-decoration: none;
}
.product .product__title {
  margin: 0;
  font-size: 16px;
  text-transform: uppercase;
  color: #222222;
  margin-bottom: 20px;
  font-weight: 400;
}
.product .product__description-list {
  margin: 0;
  margin-bottom: 22px;
  padding: 0;
  font-family: "Arial", sans-serif;
  font-size: 12px;
  line-height: 16px;
  color: #444444;
  list-style-type: none;
}
.product .product__description-list-data {
  margin: 0;
}
.product .product__button {
  min-width: 280px;
  text-decoration: none;
  padding: 10px;
}
.product .product__button--more {
  color: #444444;
  background-color: #f2f2f2;
}
.product .product__text-show {
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  color: #111111;
  margin-bottom: 8px;
}
.product .product__text {
  font-family: "Arial", sans-serif;
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  color: #444444;
  margin-bottom: 21px;
}
.product-show-more {
  padding: 0;
}
.catalog-additional {
  padding-top: 25px;
  margin-bottom: 35px;
}
.catalog-additional .catalog-additional__title {
  margin: 0;
  margin-bottom: 36px;
  font-size: 24px;
  line-height: 24px;
  font-weight: 400;
}
.catalog-additional .catalog-additional__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.additional-product {
  padding-top: 13px;
  padding-bottom: 19px;
}
.additional-product .additional-product__title {
  margin: 0;
  margin-bottom: 13px;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  color: #222222;
}
.additional-product .additional-product__data {
  font-family: "Arial", sans-serif;
  line-height: 14px;
  color: #444444;
  margin-bottom: 15px;
}
.additional-product .additional-product__button {
  min-width: 280px;
  text-decoration: none;
  padding: 10px;
}
.catalog-gift {
  margin: 0 20px 35px;
  padding-top: 58px;
  padding-bottom: 43px;
  background-color: rgba(104, 183, 56, 0.85);
}
.catalog-gift .catalog-gift__text {
  margin: 0;
  font-size: 16px;
  line-height: 20px;
  color: #ffffff;
}
.main-form-title {
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  margin: 0;
  margin-bottom: 37px;
}
.form-description {
  margin: 0;
  margin-bottom: 14px;
  text-transform: uppercase;
}
.form .form__item {
  margin: 0;
  padding: 25px 0 23px;
  border: none;
}
.form .form__item:last-of-type {
  padding-bottom: 0;
  margin-bottom: 34px;
}
.form .form__item-legend {
  margin: 0;
  padding: 0;
  font-size: 24px;
  line-height: 26px;
  margin-bottom: 35px;
}
.form .form__item-container {
  margin-bottom: 14px;
}
.form .form__item-label {
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  color: #444444;
}
.form .form__item-input {
  margin: 0;
  font: inherit;
  padding: 13px;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  color: #444444;
}
.form .form__item-radio {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.form .form__item-radio-button {
  margin-bottom: 12px;
}
.form .form__item-radio-button:last-of-type {
  margin-bottom: 0;
}
.form .form__item-radio-button-label-text {
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  color: #222222;
}
.form .form__item-checkbox {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.form .form__item-checkbox-button {
  margin-bottom: 12px;
}
.form .form__item-checkbox-button:last-of-type {
  margin-bottom: 0;
}
.form .form__item-checkbox-button-label-text {
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
}
.form .form__button {
  margin-bottom: 21px;
  padding: 7px 44px;
}
.form .form__text {
  display: block;
  font-size: 14px;
  line-height: 16px;
}
.contacts-background {
  background-color: #f2f2f2;
}
.form-wrapper {
  margin: 0 20px;
  padding-top: 27px;
  padding-bottom: 32px;
}
